<template>
  <div>
    <v-container class="noticias__container">
      <title-ribbon v-bind="{ title: 'Noticias Terapia' }" />
      <v-row class="flex-column-reverse flex-md-row">
        <v-col class="noticias__card col-12 col-md-8">
          <card-noticias
            v-for="(noticia, index) in noticias"
            :key="index"
            :noticia="{
              Id: noticia.Id,
              Titulo: noticia.Titulo,
              Texto: noticia.Texto.replace(/(\\r)*\\n/g, ''),
              Imagen: noticia.Imagen,
              VersionImagen: noticia.VersionImagen,
              Fecha: noticia.Fecha,
            }"
          />
          <v-pagination
            v-model="pagina"
            :length="Number(totalPaginas)"
            prev-icon="mdi-menu-left"
            next-icon="mdi-menu-right"
            previous-aria-label="ps"
            next-aria-label="sd"
            color="warning"
          />
        </v-col>
        <v-col class="noticias__rightbar col-12 col-md-4">
          <div
            color="transparent"
            style="border: 0px solid transparent;"
            class="pt-7"
          >
            <div class="noticias__rightbar_nosotros d-none d-md-block pb-7">
              <h4>Nosotros</h4>
              <p class="text-justify">
                Bienvenidos al Centro de Terapia del Comportamiento, un Centro
                de Salud Mental privado con más de 40 años al servicio de la
                comunidad y que reúne un equipo de Psicólogos, Psiquiatras,
                Neurólogos, Fonoaudiólogos, Psicopedagogos, Nutricionistas, y
                Terapeutas Ocupacionales, donde cualquier persona puede acceder
                a servicios especializados de calidad a través de los distintos
                sistemas previsionales y por valores razonables al alcance de la
                mayoría.
              </p>
            </div>
            <div class="noticias__rightbar_buscar text-center text-md-left">
              <h4>Buscar en el Blog</h4>

              <v-row>
                <v-col class="col-12 col-md-7">
                  <v-text-field
                    v-model="search"
                    color="info"
                    :loading="loading"
                    label="Busca una noticia"
                  />
                </v-col>
              </v-row>
              <v-row>
                <v-col class="col-12 d-none d-md-block">
                  <v-list-item-group color="primary">
                    <v-list-item
                      v-for="(item, i) in items"
                      :key="i"
                      dense
                      color="transparent"
                      @click="search = item.searchParam"
                    >
                      <v-list-item-icon>
                        <v-icon v-text="item.icon" />
                      </v-list-item-icon>
                      <v-list-item-content>
                        <v-list-item-title
                          style="color: #3a9cba; !important"
                          v-text="item.text"
                        />
                      </v-list-item-content>
                    </v-list-item>
                  </v-list-item-group>
                </v-col>
              </v-row>
              <v-row class="d-none d-md-block">
                <v-col cols="12 text-center text-md-left">
                  <siguenos />
                </v-col>
              </v-row>
            </div>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
  import Vue from 'vue'
  export default {
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      CardNoticias: () => import('@/components/web/base/CardNoticias'),
      Siguenos: () => import('@/components/web/base/Siguenos'),
    },
    data () {
      return {
        noticias: [],
        totalNoticias: '',
        totalPaginas: '',
        pagina: 1,
        limite: 6,
        search: '',
        loading: false,
        items: [
          {
            text: 'Alimentación Sana',
            icon: 'mdi-chevron-right',
            searchParam: 'Alimentación Sana',
          },
          {
            text: 'Emociones',
            icon: 'mdi-chevron-right',
            searchParam: 'Emociones',
          },
          {
            text: 'Estrés',
            icon: 'mdi-chevron-right',
            searchParam: 'Estrés',
          },
          {
            text: 'Infancia',
            icon: 'mdi-chevron-right',
            searchParam: 'Infancia',
          },
          {
            text: 'Mente Positiva',
            icon: 'mdi-chevron-right',
            searchParam: 'Mente Positiva',
          },
          { text: 'Terapia', icon: 'mdi-chevron-right', searchParam: 'terapia' },
          {
            text: 'Parejas',
            icon: 'mdi-chevron-right',
            searchParam: 'Parejas',
          },
          {
            text: 'Terapia Online',
            icon: 'mdi-chevron-right',
            searchParam: 'Terapia Online',
          },
          {
            text: 'Trastorno Bipolar',
            icon: 'mdi-chevron-right',
            searchParam: 'Trastorno Bipolar',
          },
        ],
      }
    },
    watch: {
      pagina: function () {
        // console.log(this.pagina)
        if (this.search) {
          this.paginacion(this.pagina, this.search)
        } else {
          this.paginacion(this.pagina, '')
        }
      },
      search: function () {
        // console.log(this.search)
        this.pagina = 1 // Seteamos la página en 1 por si pagineó antes
        if (this.search) {
          this.paginacion(this.pagina, this.search)
        } else {
          this.paginacion(this.pagina, '')
        }
      },
    },
    created () {
      this.paginacion(this.pagina)
    },
    methods: {
      paginacion (pagina, byTitleParam = '') {
        const limite = this.limite
        const desde = (pagina - 1) * limite
        const bytitle = byTitleParam.split(' ').filter((a) => a)

        const body = {
          limite,
          desde,
          bytitle,
        }

        Vue.axios
          .post('/api/noticias', body)
          .then(res => {
            this.noticias = res.data.result
            this.totalNoticias = res.data.total
            this.totalPaginas = Math.round(res.data.total / this.limite) // Redondeamos el límite
          })
          .catch(e => {
            console.log(e.response)
          })
      },
    },
    metaInfo: {
      title: 'Noticias',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
